html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  background: #545065;
  position: relative;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
header {
  width: 100%;
  margin: 10px 0;
  color: #fff;
  text-align: center;
}

footer {
  width: 100%;
  background: #000;
  padding: 10px;
  box-sizing: border-box;
}

footer p {
  color: #fff;
  margin: 0;
  text-align: center;
}

footer a {
  color: #00b6ff;
}

.task-form {
  position: relative;
  overflow: hidden;
  border-radius: 5px;
}

.task-form .input-text {
  width: 100%;
  padding: 10px 45px 10px 10px;
  box-sizing: border-box;
  border: none;
}

.form-submit {
  position: absolute;
  top: 0;
  right: 0;
  width: 34px;
  font-size: 40px;
  line-height: 26px;
  color: #58d485;
  text-align: center;
  font-weight: bold;
  text-decoration: none;
  height: 100%;
}

.filters {
  display: flex;
  justify-content: center;
  color: #fff;
  padding: 20px 0;
}

.filter-link {
  margin-right: 10px;
  color: darkgrey;
  text-decoration: none;
}

.filter-link:last-child {
  margin-right: 0;
}

.filter-link:hover,
.filter-link.active {
  color: #fff;
  text-decoration: underline;
}

.task-item {
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 10px;
}

.task-card {
  width: 100%;
  position: relative;
  border-radius: 6px;
  background: #4a9bcc;
  text-align: center;
  color: #fff;
  font-weight: 900;
  box-shadow: 0px 3px 12px -3px rgba(0, 0, 0, 0.45);
  overflow: hidden;
  border-radius: 10px;
  border: 2px solid transparent;
  transition: background 0.5s;
}

.task-card.completed {
  background: #424242;
}

.task-card.edited {
  border: 2px solid #29c0e0;
}

.task-text {
  width: 100%;
  height: 100%;
  display: block;
  padding: 20px 42px;
  box-sizing: border-box;
  border: none;
  outline: none;
}

.task-card.completed .task-text {
  color: #7d7d7d;
  -webkit-text-decoration: line-through solid #ccc;
          text-decoration: line-through solid #ccc;
}

.task-card input.task-text {
  font-size: 17px;
  text-align: center;
  font-weight: 600;
  color: #383b44;
}

.task-edit,
.task-cancel,
.task-delete,
.task-save {
  display: block;
  position: absolute;
  top: 2px;
  text-decoration: none;
  font-size: 18px;
  color: #377090;
  font-weight: 900;
  transition: color 0.5s;
}

.task-edit,
.task-cancel {
  left: 10px;
}

.task-delete,
.task-save {
  right: 10px;
}

.task-edit,
.task-delete {
  display: none;
}

.task-item:hover .task-edit,
.task-item:hover .task-delete {
  display: block;
}

.task-card.completed .task-edit,
.task-card.completed .task-delete {
  color: #7d7d7d;
}

.task-card.edited .task-cancel,
.task-card.edited .task-save {
  color: #b0b0b0;
}

.task-edit:hover,
.task-card.completed .task-edit:hover {
  color: orange;
}

.task-delete:hover,
.task-card.completed .task-delete:hover {
  color: red;
}

.task-card.edited .task-cancel:hover {
  color: orange;
}

.task-card.edited .task-save:hover {
  color: #2fc766;
}

.task-checkbox input[type="checkbox"] {
  display: none;
}

.proxy-checkbox {
  display: block;
  flex-shrink: 0;
  margin-right: 20px;
  width: 40px;
  height: 40px;
  border: 2px solid #fff;
  border-radius: 8px;
  cursor: pointer;
}

.proxy-checkbox.checked::after {
  content: "\2714";
  display: block;
  color: #58d485;
  font-size: 36px;
  line-height: 35px;
  text-align: center;
}

.task-list .loader,
.task-list .message {
  color: #00ffc6;
  text-align: center;
  font-size: 20px;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}

.main {
  flex: 1 1;
  width: 40%;
}

